@-webkit-keyframes movePosters {
  0% {
    -webkit-transform: translate3d(0, -8px, 0);
    transform: translate3d(0, -8px, 0);
  }
  to {
    -webkit-transform: translate3d(0, -700px, 0);
    transform: translate3d(0, -700px, 0);
  }
}
@keyframes movePosters {
  0% {
    -webkit-transform: translate3d(0, -8px, 0);
    transform: translate3d(0, -8px, 0);
  }
  to {
    -webkit-transform: translate3d(0, -700px, 0);
    transform: translate3d(0, -700px, 0);
  }
}
@-webkit-keyframes movePostersAlt {
  0% {
    -webkit-transform: translate3d(0, -700px, 0);
    transform: translate3d(0, -700px, 0);
  }
  to {
    -webkit-transform: translate3d(0, -10px, 0);
    transform: translate3d(0, -10px, 0);
  }
}
@keyframes movePostersAlt {
  0% {
    -webkit-transform: translate3d(0, -700px, 0);
    transform: translate3d(0, -700px, 0);
  }
  to {
    -webkit-transform: translate3d(0, -10px, 0);
    transform: translate3d(0, -10px, 0);
  }
}
.MyEmptyListCta {
  display: block;
  font-family: "Roboto", sans-serif;
  margin: 0 auto 32px;
  position: relative;
  width: 100%;
  z-index: 10;
}
.MyEmptyListCta__container__title {
  display: block;
  font-weight: 200;
  letter-spacing: normal;
  line-height: 1;
  margin-bottom: 15px;
  margin-top: 0;
  text-transform: uppercase;
}
.MyEmptyListCta__container__title h2 {
  font-size: 24px;
}
.MyEmptyListCta__container__block {
  background: linear-gradient(to right, #0b135c 0%, rgba(11, 19, 92, 0.5) 60%, rgba(11, 19, 92, 0) 100%);
  display: block;
  overflow: hidden;
  position: relative;
}
.MyEmptyListCta__container__block__content {
  padding: 42px 16px;
  position: relative;
  text-align: center;
  z-index: 10;
}
.MyEmptyListCta__container__block__content__subtitle {
  color: #ffffff;
  font-size: 22px;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 1;
}
.MyEmptyListCta__container__block__content__subtitle svg {
  margin-bottom: 15px;
}
.MyEmptyListCta__container__block__content__description {
  color: #ffffff;
  display: block;
  font-size: 14px;
  font-weight: 200;
  letter-spacing: 0.5px;
  line-height: 1.38;
  margin-top: 7px;
}
.MyEmptyListCta__container__block__container-parallax {
  display: none;
  max-height: 1400px;
  overflow: visible;
}
.MyEmptyListCta__container__block__container-parallax__shows-list {
  display: inline-block;
  font-size: 0;
  margin-right: 16px;
  max-width: 194px;
  position: relative;
  top: 60px;
  vertical-align: top;
}
.MyEmptyListCta__container__block__container-parallax__shows-list:first-child {
  -webkit-animation: movePosters 60s linear 0s infinite alternate;
  animation: movePosters 60s linear 0s infinite alternate;
}
.MyEmptyListCta__container__block__container-parallax__shows-list:nth-child(2) {
  -webkit-animation: movePostersAlt 60s linear 0s infinite alternate;
  animation: movePostersAlt 60s linear 0s infinite alternate;
  top: 236px;
}
.MyEmptyListCta__container__block__container-parallax__shows-list:last-child {
  -webkit-animation: movePosters 60s linear 0s infinite alternate;
  animation: movePosters 60s linear 0s infinite alternate;
  margin-right: 0;
}
.MyEmptyListCta__container__block__container-parallax__shows-list li {
  margin-bottom: 16px;
}
.btn-wrapper {
  display: flex;
  justify-content: center;
  /* center items horizontally */
  align-items: center;
  /* center items vertically */
}
.btn-wrapper span {
