.modal {
  border-radius: 1rem;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.25);
  width: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 2em;
  z-index: 122;
  max-width: 250px;
  font-size: 14px;
  border: 2px solid #7f89e7;
  background: linear-gradient(to top, #0b135c, #000000);
  color: #fff;
  animation: gradientAnimation 10s ease-in-out infinite;
}
.modal .close {
  float: right;
}
.modal img {
  max-height: 400px;
  width: 100%;
  display: block;
  margin: 0 auto;
}
.modal:not(.active) {
  display: none;
}
.modal .close svg {
  width: 20px;
  height: 20px;
}
.modal h2 {
  margin: 10px 0 20px;
}
.modal p {
  margin-bottom: 10px;
}
.modal .footer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  border-top: 1px solid grey;
  padding-top: 10px;
  margin-top: 5px;
}
.modal .footer .star-rating {
  color: #ff9900;
}
/* Define the keyframes for the animation */
@keyframes gradientAnimation {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 0% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}
